import React, { PureComponent } from "react";
import QRCode from "react-qr-code";
import { connect } from "react-redux";
import { ServerUrl, TagShopServerUrl, SocialWallServerUrl, SocialWallEmbedServerUrl, SocialWallServerTestUrl } from '../../../../constants'
import { onSitePopup } from '../../../../actions/themeActions'
import { isTagShop, isSocialWall, isSocialEmbed, FOR_TEST_SERVER } from '../../../../actions/api'
class OnsiteUploadButton extends PureComponent {

  render() {
    const { UgcSettings, id, tagShopWallId } = this.props;
    var ugcSuite = window.ugcSuite ? { ugcSuite: 1 } : window.location.href.includes("ugcSuite=1") ? { ugcSuite: 1 } : {}
    const isUgcSuite = Object.keys(ugcSuite).length > 0 ? '&ugcSuite=1' : ''

    const isTagshopWall = !tagShopWallId && isTagShop ? true : false

    const QRButtonText = UgcSettings.onsite_qr_code_status || UgcSettings.onsite_qr_code_btn_text != '' ? UgcSettings.onsite_qr_code_btn_text : ''
    const QRLink = isTagShop ? `${TagShopServerUrl}/${id}/onsite-upload${isTagshopWall ? '?wall=1' : ''}` : `${isSocialWall && !isSocialEmbed ? FOR_TEST_SERVER ? SocialWallServerTestUrl : SocialWallServerUrl : isSocialWall && isSocialEmbed ? SocialWallEmbedServerUrl : ServerUrl}/${id}?onsite-upload=true${isUgcSuite}`

    const wallQRClass = isSocialWall && !isSocialEmbed ? QRButtonText.length < 31 ? 'tb_snap_btn_wall tb_snap_btn_wall_v' : 'tb_snap_btn_wall' : ''
    const position = UgcSettings?.onsite_overlay_poisition ? ` tb_pos-${UgcSettings.onsite_overlay_poisition}` : null
    return UgcSettings.onsite_status && (isTagShop || UgcSettings.onsite_button_status) && (UgcSettings.onsite_qr_code_status == 1 || QRButtonText !== '') ? <div className={`tb_onsite_upload_btn_wrap ${wallQRClass}${position}`}>
      <div className="tb_onsite_upload_btn">
        <div className="tb_snap_btn_bg" style={{ opacity: isSocialWall ? 0.7 : null }}><div></div></div>
         <div className="tb_onsite_btn" onClick={() => this.props.onSitePopup(window.isSocialEmbed)}>
          <div className="tb_onsite_qr_btn_wrap">
            {UgcSettings.onsite_qr_code_status == 1 ? <div className='tb_onsite_qr_code'>
              <QRCode level="L" size={60} value={QRLink} />
            </div> : null}
            {QRButtonText !== '' ?
              <div className={`tb_onsite_btn_txt tb-cTBfont-${UgcSettings.onsite_font_varient_btn}`}>
                {QRButtonText}
              </div> : ''}
          </div>
        </div>
      </div>
    </div> : null
  }
}
const mapStateToProps = state => {
  return {
    modalPop: state.modalPop
  }
}
const mapDispatchToProps = dispatch => {
  return {
    onSitePopup: (status) => dispatch(onSitePopup(status)),
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(OnsiteUploadButton);